/* General Container Styling */
.activity-log-container {
    max-width: 1200px;
    margin: 50px auto;
    padding: 30px;
    background-color: #fff;
    border-radius: 15px;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
    font-family: 'Arial', sans-serif;
}

/* Heading Styling */
.activity-log-container h2 {
    text-align: center;
    margin-bottom: 30px;
    font-size: 32px;
    color: #333;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1.2px;
}

/* Table Wrapper Styling */
.activity-log-table-wrapper {
    max-height: 400px;
    overflow-y: auto;
    border-radius: 15px;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
}

/* Scrollbar Styling */
.activity-log-table-wrapper::-webkit-scrollbar {
    width: 12px;
}

.activity-log-table-wrapper::-webkit-scrollbar-thumb {
    background: #ff416c;
    border-radius: 10px;
    border: 3px solid #fff;
}

.activity-log-table-wrapper::-webkit-scrollbar-track {
    background: #f1f1f1;
    border-radius: 10px;
}

/* Table Styling */
.activity-log-table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0 15px;
}

/* Table Header Styling */
.activity-log-table th {
    padding: 15px;
    text-align: left;
    background-color: #f2f2f2;
    color: #333;
    font-weight: bold;
    border-bottom: 2px solid #ddd;
    text-transform: uppercase;
    position: sticky;
    top: 0;
    z-index: 1; /* Ensure the header stays on top */
}

/* Table Cell Styling */
.activity-log-table td {
    padding: 15px;
    background-color: #fff;
    color: #333;
    font-size: 16px;
    border-bottom: 1px solid #eee;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.05);
    border-radius: 8px;
}

/* Row Hover Effect */
.activity-log-table tr:hover td {
    background-color: #fafafa;
    transition: background-color 0.3s ease;
}

/* Responsive Design */
@media (max-width: 768px) {
    .activity-log-table thead {
        display: none;
    }

    .activity-log-table, .activity-log-table tbody, .activity-log-table tr, .activity-log-table td {
        display: block;
        width: 100%;
    }

    .activity-log-table tr {
        margin-bottom: 15px;
    }

    .activity-log-table td {
        text-align: right;
        padding-left: 50%;
        position: relative;
    }

    .activity-log-table td::before {
        content: attr(data-label);
        position: absolute;
        left: 15px;
        width: calc(50% - 30px);
        text-align: left;
        font-weight: bold;
        text-transform: uppercase;
    }
}
