.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #F9FAFB;
    color: #36454F;
    padding: 10px;
    transition: margin-left 0.3s ease;
    z-index: 500; /* Ensure header stays above other content */
  }
  
  .header .menu-icon {
    cursor: pointer;
  }
  
  .header .faChevron {
    font-size: 20px;
  }
  
  .header .faChevron.rotate {
    transform: rotate(180deg);
  }
  
  .header .page-title {
    flex-grow: 1;
    text-align: center;
    font-size: 12px;
    font-weight: 500;
  }
  
  .header .header-icons {
    display: flex;
    align-items: center;
  }
  
  .header .header-icons .icon {
    margin-left: 15px;
    cursor: pointer;
  }
  
  body.dark-mode .header {
    background-color: #2C2C2C;
    color: #E0E0E0;
  }
  