.user-panel-sidebar {
    width: 250px;
    height: 100%;
    background-color: #333;
    color: #fff;
  }
  
  .user-panel-sidebar ul {
    list-style: none;
    padding: 0;
  }
  
  .user-panel-sidebar li {
    padding: 10px;
  }
  
  .user-panel-sidebar a {
    text-decoration: none;
    color: #fff;
    display: flex;
    align-items: center;
  }
  
  .user-panel-sidebar a:hover {
    background-color: #555;
  }
  
  .user-panel-sidebar span {
    margin-left: 10px;
  }
  