/* Dashboard.css */

.dashboard-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    padding: 20px;
  }
  
  .card {
    background-color: #ffffff;
    border: 1px solid #dddddd;
    border-radius: 8px;
    padding: 20px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    transition: box-shadow 0.3s ease;
  }
  
  .card:hover {
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
    background-color: #ff9900;
    color: white;
    transition: background 0.3s ease-out; /* Adjust the duration and timing function */
  }
  
  .card.selected {
    background-color: red;
  }
  
  .card-content {
    display: flex;
    align-items: center;

  }
  
  .icon {
    margin-right: 20px;
  }
  
  .info {
    flex-grow: 1;
  }

  .info > h3{
   font-size: 40px;
  }
  .more-info {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
    color: #676767;
    font-size: 14px;
    cursor: pointer;
  }
  
  .more-info span {
    margin-left: 5px;
  }
  