/* General Container Styling */
.invoice-container {
    max-width: 1200px;
    margin: 50px auto;
    padding: 30px;
    background-color: #fff;
    border-radius: 15px;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
    font-family: 'Arial', sans-serif;
}

/* Heading Styling */
.invoice-container h2 {
    text-align: center;
    margin-bottom: 30px;
    font-size: 32px;
    color: #333;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1.2px;
}

/* Table Wrapper Styling */
.invoice-table-wrapper {
    max-height: 400px;
    overflow-y: auto;
    border-radius: 15px;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
}

/* Scrollbar Styling */
.invoice-table-wrapper::-webkit-scrollbar {
    width: 12px;
}

.invoice-table-wrapper::-webkit-scrollbar-thumb {
    background: #ff416c;
    border-radius: 10px;
    border: 3px solid #fff;
}

.invoice-table-wrapper::-webkit-scrollbar-track {
    background: #f1f1f1;
    border-radius: 10px;
}

/* Table Styling */
.invoice-table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0 15px;
}

/* Table Header Styling */
.invoice-table th {
    padding: 15px;
    text-align: left;
    background-color: #f2f2f2;
    color: #333;
    font-weight: bold;
    border-bottom: 2px solid #ddd;
    text-transform: uppercase;
    position: sticky;
    top: 0;
    z-index: 1; /* Ensure the header stays on top */
}

/* Table Cell Styling */
.invoice-table td {
    padding: 15px;
    background-color: #fff;
    color: #333;
    font-size: 16px;
    border-bottom: 1px solid #eee;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.05);
    border-radius: 8px;
}

/* Row Hover Effect */
.invoice-table tr:hover td {
    background-color: #fafafa;
    transition: background-color 0.3s ease;
}

/* Action Button Styling */
.action-btn {
    padding: 8px;
    margin: 10px 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
    transition: background-color 0.3s;
    display: inline-flex;
    align-items: center;
}

.delete-btn {
    background-color: #dc3545;
    color: #fff;
}

.delete-btn:hover {
    background-color: #c82333;
    transform: scale(1.05);
}

.download-btn {
    color: #007bff;
    font-size: 18px;
    text-decoration: none;
}

.download-btn:hover {
    color: #0056b3;
    transform: scale(1.05);
}

/* Responsive Design */
@media (max-width: 768px) {
    .invoice-table thead {
        display: none;
    }

    .invoice-table, .invoice-table tbody, .invoice-table tr, .invoice-table td {
        display: block;
        width: 100%;
    }

    .invoice-table tr {
        margin-bottom: 15px;
    }

    .invoice-table td {
        text-align: right;
        padding-left: 50%;
        position: relative;
    }

    .invoice-table td::before {
        content: attr(data-label);
        position: absolute;
        left: 15px;
        width: calc(50% - 30px);
        text-align: left;
        font-weight: bold;
        text-transform: uppercase;
    }
}
