.sidebar {
  width: 250px;
  background-color: #F9FAFB;
  color: #36454F;
  height: 100vh;
  position: fixed;
  overflow-y: auto; /* Enable vertical scrolling */
  -webkit-overflow-scrolling: touch;
  transition: transform 0.3s ease-in-out; /* Transition for sidebar */
}

.sidebar.closed {
  transform: translateX(-100%);
}

.logo {
  text-align: center;
  padding: 20px 0;
}

.logo img {
  width: 100px;
}

.sidebar-options {
  list-style: none;
  padding: 0;
  margin: 0; /* Add this to reset margin */
}

.sidebar-option {
  display: flex;
  align-items: center;
  padding: 15px 20px;
  cursor: pointer;
  transition: background-color 0.3s;
  font-size: 13px;
  position: relative;
}

.sidebar-option:hover {
  background-color: #34495e;
}

.icon {
  margin-right: 10px;
}

.dropdown {
  list-style: none;
  padding: 0;
  margin: 0;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-in-out; /* Animation for dropdown */
}

.dropdown.open {
  max-height: 500px; /* Adjust this value as needed */
}


.dropdown.open li {
  opacity: 1;
  transform: translateY(0);
}
.dropdown li {
  padding: 10px 40px; /* Adjust padding to create space between options */
  cursor: pointer;
  transition: background-color 0.3s;
  font-size: 12px;
  font-weight: 390;
  opacity: 0;
  transform: translateY(-10px);
}

.dropdown li:hover {
  background-color: #3b5998;
}

.arrow {
  position: absolute;
  right: 20px;
  margin-left: auto;
}

.selected {
  background-color: #34495e; /* Adjust the selected background color */
  color: #fff; /* Adjust the text color for better visibility */
}

.header {
  display: flex;
  align-items: center;
  background-color: #F9FAFB;
  padding: 10px;
}

.menu-icon {
  cursor: pointer;
  padding: 10px;
}

.title {
  flex-grow: 1;
  text-align: center;
}

.sidebar.collapsed {
  width: 0; /* Set width to 0 when collapsed */
}